/* skip nav link */
#skip {
    left: 12px;
    top: -100px;
    position: fixed;
    -ie-transition: all 0.3s;
    -o-transition: all 0.3s;
    -moz-transition: all 0.3s;
    -webkit-transition: all 0.3s;
    transition: top 0.3s;
    z-index: 10000;
}

#skip:focus {
    top: 12px;
}
